:root {
  --flex-item: 5;
  --flex-item-gap: 20px;
  --main-text-color: #111;
  --main-text-color-60: rgba(17, 17, 17, .6);
  --extra-text-color: #4f2ee8;
  --sub-text-color: #b4afaf;
  --light-text-color: #fff;
  --light-text-color-60: rgba(255, 255, 255, .6);
  --place-text-color: #eac645;
  --dark-text-color-60: rgba(0, 0, 0, .6);
  --dark-bg-color-60: rgba(17, 17, 17, .6);
  --main-bg-color: #111;
  --extra-bg-color: #4f2ee8;
  --place-bg-color: #eac645;
  --bg-color: #f6f6f6;
  --modal-bakdrop: rgba(17, 17, 17, .4);
  --modal-bg: #fff;
  --extra-border-color: #4f2ee8;
  --extra-border-color-40: rgba(79, 46, 232, .4);
  --modal-border-wrap-color: #111;
  --empty: #fff;
  --transition-property: color, background, box-shadow, transform;
  --transition-duration: .3s;
  --transition-timing-function: ease-out;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  border-radius: 12px;
  transition: all .3s ease-in-out;
}

body.light-theme::-webkit-scrollbar-thumb {
  background: #e0e0e0;
}

body.dark-theme::-webkit-scrollbar-thumb {
  background: var(--dark-bg-color-60);
}

body::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, .4);
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--main-text-color);
  letter-spacing: -.04em;
  padding-bottom: 80px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

@media screen and (min-width: 768px) {
  body {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1440px) {
  body {
    padding-bottom: 72px;
  }
}

body.light-theme {
  background: #f6f6f6;
}

body.dark-theme {
  background: #202024;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
