  }
}

.global-title, .global-p {
  margin: 0;
}

.global-link {
  color: currentColor;
  text-decoration: none;
}

.global-button {
  color: currentColor;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  letter-spacing: inherit;
  cursor: pointer;
  border: none;
}

.global-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.flex-container {
  gap: var(--flex-item-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex-container-item {
  flex-basis: calc( (100% - (var(--flex-item)  - 1) * var(--flex-item-gap) ) / var(--flex-item) );
}

.main-block > .container {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
