}

.link-text.shopping-link:before {
  content: "";
  width: 20px;
  height: 20px;
  background-size: 20px;
  margin-right: 8px;
  display: inline-block;
}

.sprite-image.light-theme {
  background-image: url("icons.2b18bf27.svg#icon-shop");
  background-repeat: no-repeat;
}

.sprite-image.dark-theme {
  fill: var(--light-text-color);
  background-image: url("icons.2b18bf27.svg#icon-shop-dark");
  background-repeat: no-repeat;
}

.switcher {
  width: 40px;
  height: 24px;
  background: linear-gradient( 180deg, var(--extra-bg-color) 0%, var(--light-text-color-60) 100% );
  cursor: pointer;
  border-radius: 12px;
  margin-left: auto;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switcher.active {
  background: linear-gradient(180deg, #4f2ee8 0%, var(--sub-text-color) 100%);
}

.switcher-handle {
  width: 20px;
  height: 20px;
  background: -20px 0 no-repeat;
  background-color: var(--light-text-color);
  border-radius: 50%;
  transition: left .3s ease-in-out;
  position: relative;
  top: 2px;
  left: 2px;
}

.switcher-circle {
  width: 18px;
  height: 18px;
  fill: var(--light-text-color);
  position: absolute;
  top: 3px;
  left: 1px;
}

.switcher.active .switcher-handle {
  background-position: 0 0;
  left: calc(100% - 22px);
}

.user-profile {
  width: 32px;
  height: 32px;
  cursor: pointer;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  border: 0;
  border-radius: 50%;
  align-items: center;
  margin-left: 24px;
  padding: 5px;
  transition: all .25s;
  display: none;
  position: relative;
}

.user-profile:before {
  content: "";
  background-color: inherit;
  pointer-events: none;
  opacity: 0;
  border-radius: 50%;
  padding: 5px 10px;
  font-size: 14px;
  font-weight: bolder;
  transition: opacity .3s;
  position: absolute;
  left: 190%;
  transform: translateX(-50%);
}

.user-profile:hover:before {
  content: "LogIn ☼";
  opacity: 1;
  font-size: x-small;
}

.user-profile.light-theme:hover:before {
  text-shadow: 2px 2px 2px rgba(0, 0, 0, .518);
  color: rgba(1, 1, 1, .82);
}

.user-profile.dark-theme:hover:before {
  text-shadow: 2px 2px 2px rgba(234, 13, 220, .518);
  color: rgba(247, 242, 242, .82);
}

.user-profile .avatar {
  display: flex;
}

.user-profile .text {
  pointer-events: none;
  opacity: 1;
  transition: opacity .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.user-profile:hover .text {
  opacity: 0;
}

.user-profile:active {
  transform: translateY(-5px);
}

.user-profile.light-theme {
  background-color: var(--light-text-color);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1);
}

.user-profile.dark-theme {
  background-color: var(----main-text-color-text-color);
  box-shadow: 1px 1px 5px 1px #46034e;
}

